import React, { Component } from 'react';
import styled from 'styled-components';
import { ProductConsumer } from '../ContextApi/Context';
import { Button } from '../StyleComponents/Button';
import { Link } from 'react-router-dom';

export class Modal extends Component {
    render() {
        return (
            <ProductConsumer>
                {(value) => {
                    const {modalOpen, closeModal} = value;
                    const { id, title, img, price } = value.modalProduct;
                    // Conditional rendering of modal based on if the modalOpen is true or false
                    if(modalOpen === false) {
                        return null
                    } else {
                        return(
                            <ModalContainer>
                                <div className="container">
                                    <div className="row">
                                        <div id="modal" className="col-8 mx-auto col-md-6 col-sm-4 text-center text-capitalize p-5">
                                            <h5> Item Added To The Cart </h5>
                                            <img src={img} className="img-fluid" alt="product" />
                                            <h5> {title} </h5>
                                            <h5 className="text-muted">price: ₦ {price}</h5>
                                            <Link to="/">
                                                <Button onClick={() => closeModal()}>Store</Button>
                                            </Link>
                                            <Link to="/cart">
                                                <Button onClick={() => closeModal()} cart>Go to Cart</Button>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </ModalContainer>
                        )
                    }
                }}
            </ProductConsumer>
        )
    }
}

const ModalContainer = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    #modal {
        background: var(--mainWhite)
    }
`

export default Modal
